@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;

  .page-title {
    @extend %page-title;
    padding-bottom: 20px;
  }

  .page-card {
    @extend %page-card;
    margin-top: 30px;
  }

  .card-title {
    @extend %card-title;
    padding-bottom: 0;
  }

  .section-divider {
    @extend %section-divider;
  }


  .payments-list {
    &__empty {
      @include proxima(16px, 400, colors.$foreground_dark)
    }

    &__item {
      cursor: pointer;
      display: flex;
      flex-wrap: wrap;
      padding: 14px;
      background-color: #f2f2f2;
      gap: 14px;

      &-section {
        height: 48px;
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #FFFFFF;
        border-radius: 4px;

        padding: 0 8px;

        &:first-child {
          flex: 0;

          //flex-shrink: 0;

          .payments-list__item-text {
            display: flex;
            align-items: center;
            height: 48px;
            white-space: nowrap;
            padding: 0 10px;
          }
        }

        &:last-child {
          padding: unset;
        }

        &--link {
          cursor: pointer;
        }
      }

      &-mark {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #ee2a17;
        margin-right: 8px;
      }

      &-text {
        @include proxima(16px, 600, colors.$foreground_dark);
        text-align: center;

        &--status {
          @include text-shadow(1px, rgba(0, 0, 0, 0.05), 1px);
          color: #FFFFFF;
        }
      }

      &-button {
        flex-grow: 1;
        width: 100%;
      }

      &:not(:last-child) {
        border-bottom: 1px solid #E8E8E8;
      }
    }
  }

  @media screen and (max-width: 1100px) {
    .payments-list {
      &__item {
        &-section {

          &:last-child {
            flex: unset;
            width: 100%;
            flex-shrink: 0;
          }
        }
      }
    }
  }

  @media screen and (max-width: 790px) {
    .payments-list {
      &__item {
        flex-direction: column;

        &-section {
          flex: unset;
        }
      }
    }
  }
}
